.page-footer__location {
  margin-left: 105px;
  @media (max-width: $tablet-width-only) {
    margin-left: 0px; }
  @media (max-width: $mobile-width-only) {
    margin-left: 0px; } }

.page-footer__location iframe {
  min-height: 530px;
  width: 958px;
  @media (max-width: $tablet-width-only) {
    width: 90.8vw;
    min-height: 545px; }
  @media (max-width: $mobile-width-only) {
    width: 100vw;
    min-height: 250px;
    margin-left: -4.6vw; } }

.page-footer__map-interactive--invisible {
  display: none; }

.page-footer__location h2 {
  margin: 0;
  margin-bottom: 30px;
  padding-left: 360px;
  @include fstyle(30px, 30px, $very-dark-black, 600);
  letter-spacing: -0.15px;
  @media (max-width: $tablet-width-only) {
    width: 23.5vw;
    margin-bottom: 20px;
    padding-left: 34vw;
    @include fstyle(24px, 30px, $very-dark-black, 600);
    letter-spacing: -0.12px;
    text-align: center; }
  @media (max-width: $mobile-width-only) {
    width: 42.5vw;
    margin-bottom: 20px;
    padding-left: 24vw;
    @include fstyle(18px, 30px, $very-dark-black, 600);
    letter-spacing: -0.1px; } }

.page-footer__location p {
  width: 555px;
  margin: 0px;
  margin-bottom: 42px;
  padding-left: 201px;
  @include fstyle(17px, 31px, rgba($black, 0.7));
  text-align: center;
  @media (max-width: $tablet-width-only) {
    width: 82vw;
    margin-bottom: 20px;
    padding-left: 4.5vw; }
  @media (max-width: $mobile-width-only) {
    width: 83vw;
    margin-bottom: 25px;
    padding-left: 3vw; } }

.page-footer__map-wrapper {
  width: 957px;
  min-height: 530px;
  @media (max-width: $tablet-width-only) {
    width: 698px;
    min-height: 545px;
    margin: 0px auto; }
  @media (max-width: $mobile-width-only) {
    width: 320px;
    min-height: 250px;
    margin: 0px auto; } }

.page-footer__map-wrapper--invisible {
  display: none; }

.page-footer__map-wrapper img {
  width: 100%; }
